import React, { Component } from 'react';

import {
  Button,
  TextInput,
  Dropdown,
  Select,
  Tags,
  Navbar,
  Progress,
  Slider,
  Checkbox,
  Radio,
  Icon,
  Switch
 } from '../../sand-ui/lib/sand.js';

import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      animals: 'cat',
      status: true
    }
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1>Sand-UI</h1>
          <p>Stylished components for React</p>
        </header>
        <section className="App-section">
          <header>
            <h2>Components</h2>
          </header>
          <article className="container-box">
            <header>
              <h3>Buttons</h3>
            </header>
            <div className="wrapper">
              <Button>I am a Button!</Button>
            </div>
            <div className="wrapper">
              <Button type="success" link="#">Success Button</Button>
            </div>
            <div className="wrapper">
              <Button type="warning">Warning Button</Button>
            </div>
            <div className="wrapper">
              <Button type="danger">Danger Button!</Button>
            </div>
            <div className="wrapper">
              <Button type="info">Info Button</Button>
            </div>
            <div className="wrapper">
              <Button type="inverse">Primary Colored Button</Button>
            </div>
            <div className="wrapper">
              <Button type="disabled">Disabled Button</Button>
            </div>
            <div className="wrapper">
              <Button>
                <span><Icon name="phone" color="white"/> Button</span>
              </Button>
            </div>
          </article>

          <article className="container-box">
            <header>
              <h3>Inputs</h3>
            </header>
            <div className="wrapper">
              <TextInput icon="flag" />
              </div>
            <div className="wrapper">
              <TextInput placeholder="John Doe"  />
              </div>
            <div className="wrapper">
              <TextInput className='success' icon="users" placeholder="Success" />
            </div>
            <div className="wrapper">
              <TextInput className="error" icon="laptop-phone" placeholder="Error" />
              </div>
            <div className="wrapper">
              <TextInput icon="train" className="disabled" style={{ fontSize: 60 }} placeholder="disabled" />
            </div>
            <div className="wrapper">
              <TextInput type="date" />
            </div>
          </article>

          <article className="container-box">
            <header>
              <h3>Dropdown</h3>
            </header>
            <Dropdown
            label="Default"
            list={[
              { name: 'Item 1' },
              { name: 'Item 2' },
              { name: 'Item 3' },
              'Item 4'
            ]} />
          </article>

          <article className="container-box">
            <header>
              <h3>Select</h3>
            </header>
            <Select name="Avengers" options={[
              'Iron Man',
              { name: 'Captain America', value: 'ca' },
              { name: 'Black Widow', value: 'bw' }
            ]} />
          </article>

          <article className="container-box">
            <header>
              <h3>Tags</h3>
            </header>
            <Tags values={['One', 'Two', 'Threww']} onUpdate={(values) => console.log(values)} />
          </article>

          <article className="container-box">
            <header>
              <h3>Navbar</h3>
            </header>
            <Navbar title="SandUI" searchbar onSearch={(val) => console.log(val)} />
          </article>

          <article className="container-box">
            <header>
              <h3>Progress Bar</h3>
            </header>
            <div className="wrapper">
              <Progress percent={15} />
            </div>
            <div className="wrapper">
              <Progress type="info" />
            </div>
            <div className="wrapper">
              <Progress type="success" percent={65} />
            </div>
            <div className="wrapper">
              <Progress type="danger" percent={12} />
            </div>
            <div className="wrapper">
              <Progress type="warning" percent={40} />
            </div>
            <div className="wrapper">
              <Progress type="inverse" percent={90} />
            </div>
          </article>

          <article className="container-box">
            <header>
              <h3>Slider</h3>
            </header>
            <Slider step={7} onChange={(per) => console.log(per)} />
          </article>

          <article className="container-box">
            <header>
              <h3>Checkbox</h3>
            </header>
            <Checkbox name="Checkbox uno" />
            <Checkbox name="Checkbox doss" checked />
            <Checkbox name="Checkbox three" disabled />
            <Checkbox name="Final checkbox" disabled checked />
          </article>

          <article className="container-box">
            <header>
              <h3>Radio</h3>
            </header>
            <Radio value="cat" name="animals" checked={this.state.animals === 'cat'} onChange={(val) => this.setState({ animals: val })}>Cat</Radio>
            <Radio value="dog" name="animals" checked={this.state.animals === 'dog'} onChange={(val) => this.setState({ animals: val })}>Dog</Radio>
            <Radio value="dragon" name="animals" disabled>Dragon</Radio>
            <Radio value="chimera" name="fantasy" disabled checked={true}>Chimera</Radio>
          </article>

          <article className="container-box">
            <header>
              <h3>Icons</h3>
            </header>
            <article style={{height: '40px'}}>
              <Icon name="phone" color="warning" />
              <Icon name="bug" color="#00A86B" style={{ transform: 'rotate(90deg)' }} />
              <Icon name="lighter" color="active" />
            </article>
          </article>

          <article className="container-box">
            <header>
              <h3>Switch</h3>
            </header>
            <Switch status={this.state.status} onChange={(st) => this.setState({ status: st })} />
            <Switch type="box" status={this.state.status} onChange={(st) => this.setState({ status: st })} />
          </article>
        </section>
      </div>
    );
  }
}

export default App;



